import { makePipeline } from '@uniformdev/next';
import { PagePropsPipelineContext, PagePropsPipelineStep } from '../interfaces';
import { getPropsFromRenderingHostRequestStep } from './steps/getPropsFromRenderingHostRequestStep';
import { getPropsFromStaticFilesStep } from './steps/getPropsFromStaticFilesStep';
import { getPropsFromApiStep } from './steps/getPropsFromApiStep';

const preProcessSteps: PagePropsPipelineStep[] = [];

const buildPropsSteps: PagePropsPipelineStep[] = [
    getPropsFromRenderingHostRequestStep,
    getPropsFromStaticFilesStep,
    getPropsFromApiStep,
];

const postProcessSteps: PagePropsPipelineStep[] = [];

export const pagePropsPipeline = makePipeline<PagePropsPipelineContext>(
    buildPropsSteps,
    preProcessSteps,
    postProcessSteps
);
